.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  max-width: 330px;
  width: 100%;
  padding: 15px 20px;
  border-radius: 32px;
  box-sizing: border-box;
  background-color: #fe6e36;
  color: #ffffff;
  font-family: "ArialRoundedMTBold", sans-serif;
